import BScroll from 'better-scroll'
import Loading from './loading.vue'
import { getRect } from '../../utils/dom'

export default {
  name: 'BetterScroll',
  components: {
    Loading
  },
  props: {
    List: {
      type: Array,
      default: function () {
        return []
      }
    },
    probeType: {
      type: Number,
      default: 1
    },
    click: {
      type: Boolean,
      default: true
    },
    direction: {
      type: String,
      default: 'vertical'
    },
    scrollbar: {
      type: null,
      default: true
    },
    pullUpLoad: {
      type: null,
      default: false
    },
    // 纵轴方向初始化位置
    startY: {
      type: Number,
      default: 0
    },
    refreshDelay: {
      type: Number,
      default: 20
    },
    // 有些场景我们需要支持横向和纵向同时滚动，而不仅限制在某个方向，这个时候我们只要设置 freeScroll 为 true 即可。
    freeScroll: {
      type: Boolean,
      default: false
    },
    // 当滚动超过边缘的时候会有一小段回弹动画。设置为 true 则开启动画
    bounce: {
      type: Boolean,
      default: true
    },
    /**
     * 是否监听滚动 scroll 事件
     * 回调参数为{Object} {x, y} 滚动的实时坐标
     */
    listenScroll: {
      type: Boolean,
      default: false
    },
    /**
     * 是否监听滚动 beforeScrollStart 事件
     */
    listenBeforeScroll: {
      type: Boolean,
      default: false
    },
    /**
     * 是否监听滚动 touchEnd 事件
     * 回调参数为 {Object} {x, y} 滚动结束的位置坐标
     */
    listenScrollEnd: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      isPullUpLoad: false,
      pullUpDirty: true
    }
  },
  computed: {
    pullUpTxt () {
      const moreTxt =
        (this.pullUpLoad && this.pullUpLoad.txt && this.pullUpLoad.txt.more) ||
        ''
      const noMoreTxt =
        (this.pullUpLoad &&
          this.pullUpLoad.txt &&
          this.pullUpLoad.txt.noMore) ||
        '暂无更多数据'
      return this.pullUpDirty ? moreTxt : noMoreTxt
    }
  },
  mounted () {
    setTimeout(() => {
      this.initScroll()
    }, 20)
  },
  methods: {
    initScroll () {
      if (!this.$refs.wrapper) {
        return
      }
      if (this.$refs.list && (this.pullDownRefresh || this.pullUpLoad)) {
        this.$refs.list.style.minHeight = `${getRect(this.$refs.wrapper)
          .height + 1}px`
      }

      let options = {
        probeType: this.probeType,
        click: this.click,
        scrollbar: this.scrollbar,
        pullUpLoad: this.pullUpLoad,
        startY: this.startY,
        bounce: this.bounce
      }

      this.scroll = new BScroll(this.$refs.wrapper, options)

      if (this.listenScroll) {
        this.scroll.on('scroll', pos => {
          this.$emit('scroll', pos)
        })
      }

      if (this.listenBeforeScroll) {
        this.scroll.on('beforeScrollStart', () => {
          this.$emit('beforeScrollStart')
        })
      }
      if (this.listenScrollEnd) {
        this.scroll.on('scrollEnd', pos => {
          this.$emit('scrollEnd')
        })
      }
      if (this.pullUpLoad) {
        this._initPullUpLoad()
      }
    },
    refresh () {
      this.scroll && this.scroll.refresh()
    },

    destroy () {
      this.scroll.destroy()
    },
    forceUpdate (dirty) {
      if (this.pullUpLoad && this.isPullUpLoad) {
        this.isPullUpLoad = false
        this.scroll.finishPullUp()
        this.pullUpDirty = dirty
        this.refresh()
      } else {
        this.refresh()
      }
    },
    _initPullUpLoad () {
      this.scroll.on('pullingUp', () => {
        this.isPullUpLoad = true
        this.$emit('pullingUp')
      })
    }
  },
  watch: {
    data () {
      setTimeout(() => {
        this.forceUpdate(true)
      }, this.refreshDelay)
    }
  }
}



