export default {
  name: 'Login',
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    _login () {
      if (!this.username || !this.password) {
        this.$store.dispatch('setLoadingState', true)
      } else {
        this.$store.dispatch('setLoadingState', false)
        this.$router.push({
          name: 'Home'
        })
      }
    }
  }
}




