import {mapGetters} from 'vuex'
import content from '@/components/Content'
export default {
  components: {
    'v-content': content
  },
  data () {
    return {
      pageNo: 0,
      items: []
    }
  },
  computed: {
    ...mapGetters([
      'List'
    ])
  },
  methods: {
    pullup (done) {
      let params = {
        pageNo: ++this.pageNo
      }
      this.$store.dispatch('getProductList', params)
      done(true)
    },
    refresh () {
      console.log('6')
    }
  },
  mounted () {
  }
}



